.shopTitle {
    margin-top: 50px;
    text-align: center;
    font-size: 20px;
    font-family: cursive;
    text-decoration-thickness: 2px;
  }
  .products {
    width: 100%;
    flex-wrap: wrap;
    overflow: scroll;
    height: auto;
    display: flex;
    justify-content: center;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
  }
  
  .product {
    /* border: 2px solid rgb(218, 210, 210); */
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    width: 300px;
    height: 350px;
    margin: 40px;
    margin-bottom: 55px;
    margin-top: -5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .product-image {
    width: 200px;
    height: 200px;
  }
  
  .product .description {
    text-align: center;
  }
  
  .product:hover {
    transition: 0.3s ease-in;
    cursor: pointer;
    box-shadow: 10px 10px 10px rgba(80, 77, 77, 0.2);
  }
  
  .addToCartBttn {
    background-color: transparent;
    border: 2px solid rgb(109, 4, 4);
    min-width: 100px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 15px;
  }
  
  .addToCartBttn:hover {
    background-color: rgb(19, 19, 19);
    color: white;
    cursor: pointer;
  }

  .anchor {
    height: 50px;
  }